
import React, { useEffect, useState } from 'react';
import { Button, FormControl, Input, InputLabel, Paper, Typography } from '@material-ui/core';

import { useParams, useHistory } from 'react-router-dom';
import axios from 'axios';



export default function EditSanjaySubcater() {

    console.log("welcome to subedit sub catergory commpoernet");

    const { id } = useParams();

    const history = useHistory();


    const [status, setStatus] = useState(false);


    const [name, setName] = useState('');

    const [d, setD] = useState('');




    useEffect(() => {




        let getconfig = {

            method: 'get',
            url: `/api/getsansub/${id}`,

        };
        async function getsubcatergory(input) {

            try {

                const result = await axios(input);

                setName(result.data.name);


            } catch (error) {

                history.push('/sanjay-get-edit-not');


            }
        }


        getsubcatergory(getconfig);


    }, [id, history]);

    async function editsubcatergory(input) {

        try {

            const result = await axios(input);

            setD(result.data._id);
            setStatus(true);




        } catch (error) {

           


        }


    }



    function handelSubmit(e) {


        e.preventDefault();





        let config = {
            method: 'put',
            url: `/api/updatesansub/${id}`,
            data: {
                name: name,
            },

        };

        editsubcatergory(config);



    }

    function handelName(e) {

        setName(e.target.value);

    }



    function backmenu() {

        history.goBack();

    }
    return (
        <>
            {
                status ? <div>
                    <Typography>
                        edit is sucessfully done
                </Typography>

                    <Typography>
                        {d}
                    </Typography>

                    <Button onClick={backmenu}>
                        Go Back
                </Button>
                </div> : <Paper>


                    <Typography>
                        welcome to subcater edit subacterjs
            </Typography>

                    <Button onClick={backmenu}>
                        Back to pervous satatus
            </Button>
                    <form onSubmit={handelSubmit}>
                        <FormControl>

                            <InputLabel>
                                name of catergory
                    </InputLabel>

                            <Input type="text" value={name} onChange={handelName}>
                            </Input>

                        </FormControl>



                        <Button type="submit">
                            Submit
                </Button>
                    </form>
                </Paper>
            }


        </>
    )
}
