import { Paper,Box, Typography,Grid,FormControl,FormLabel,Input,Button,
TextField,MenuItem,
} from '@material-ui/core'
import axios from 'axios';
import React,{useState,useEffect} from 'react';

import {useHistory} from 'react-router-dom';

export default function ProductForm() {

    const histroy=useHistory();

    const[currencies,SetC]=useState([]);

    const[id,setId]=useState('');


    useEffect(()=>{
        async function FectMYproduct(){
try {
    let result=[];
    let ar=[];

    result=await axios({
        method:'get',
        url:'/api/public/data2',
    });


ar=result.data.map((item)=>{

    return {
        value:item,
        label:item,
    }
});

SetC(ar);
  
} catch (error) {
    

}
    
        }

        FectMYproduct();

    },[]);

    const [status,setStatus]=useState(false);

    const [name,setName]=useState('');
    const [descritiption,setDescription]=useState('');
    const[review,setReview]=useState(0);


    const [image,setImage]=useState(null);


const [buynow,setBuyNow]=useState('');



function handelName(e){

    setName(e.target.value);


}

function handelDescription(e){

    setDescription(e.target.value);

}

function handelReview(e){

    setReview(e.target.value);

}

function handelImage(e){

    setImage(e.target.files[0]);

}

function handelBuynow(e){

    setBuyNow(e.target.value);
}




function handelSubmit(e){

    e.preventDefault();

    

    if(name&&descritiption&&image&&review&&buynow&&currency){

        // from is completed 

        let formdata=new FormData();

        formdata.append('name',name);

        formdata.append('description',descritiption);

        formdata.append('reviews',review);

        formdata.append('pcatergory',currency);
        formdata.append('image',image);

        formdata.append('buynow',buynow);


        let config={
            method:'post',
            url:'/api/addproduct',
            data:formdata

        };

        addproduct(config);



    }else{

       

    }

}

    async function addproduct(input){

        try {

            const result=await axios(input);

           

            setId(result.data._id);

            setStatus(true);
            
            
        } catch (error) {

           
            
            
        }
    }


    function backmenu(){
histroy.goBack();


    }
    const [currency, setCurrency] = React.useState('');
    const handleChange = (event) => {
        setCurrency(event.target.value);
      };
    return (
        <>
        {
            status?<Paper style={{padding:'24px 16px'}}>
                <Box style={{padding:'16px 0px'}}>
                <Typography variant="h6">
                    Prouct is added sucessfully
                </Typography>
                </Box>
               <Box style={{padding:'24px 0px'}}>
               <Typography>
                   Id of Item {id}
               </Typography>
               </Box>
              

                <Button onClick={backmenu}>
                    Go Back
                </Button>
                </Paper>: <Paper style={{paddingTop:'48px',paddingBottom:'48px',paddingLeft:'16px',paddingRight:'16px'}}>
<Box textAlign="center">
<Typography variant="h6">
Product Form 
</Typography>
    </Box>
    <Grid container justifyContent="center">
<Grid item >
<form onSubmit={handelSubmit}>
    <FormControl style={{width:'100%',margin:'16px auto'}}>
        <FormLabel>
            Name of Product 
        </FormLabel>
        <Input type="text" value={name} onChange={handelName}></Input>
    </FormControl>
    <FormControl style={{width:'100%',margin:'16px auto'}}>
        <FormLabel>
            Description  of Product 
        </FormLabel>
        <Input type="text" value={descritiption} onChange={handelDescription}></Input>
    </FormControl>
    <FormControl style={{width:'100%',margin:'16px auto'}}>
        <FormLabel>
            Reviews of Product 
        </FormLabel>
        <Input type="number" value={review} onChange={handelReview}></Input>
    </FormControl>

<FormControl  style={{width:'100%',margin:'16px auto'}}>
    <FormLabel>
        Select Parent Catergory
    </FormLabel>
<TextField value={currency} select  onChange={handleChange}>
    {currencies.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}

    </TextField>
</FormControl>
<FormControl  style={{width:'100%',margin:'16px auto'}}>

<FormLabel>
    Buynow Link
</FormLabel>
<Input type="text" value={buynow} onChange={handelBuynow}>
</Input>
</FormControl>

    <FormControl  style={{width:'100%',margin:'16px auto'}}>

        <FormLabel>
            Image of Product
        </FormLabel>
<Input type="file" onChange={handelImage}>
</Input>
    </FormControl>
    <Button type="submit">
        Submit
    </Button>

</form>
</Grid>
            </Grid>
        </Paper>
        }
       
        </>
    )
}
