import { Typography } from '@material-ui/core'
import React,{useEffect} from 'react';

import axios from 'axios';

import {useParams,useHistory} from 'react-router-dom';


export default function DeleteSanjaySubcater() {

    const{id}=useParams();


    

    const history=useHistory();

    useEffect(()=>{

     
        let config={
            method:'delete',
            url:`/api/deletesansub/${id}`,
         
        };


        async function deletesubcatergory(input){

            try {
        
                await axios(input);
    
        history.goBack();
        
        
        
                
            } catch (error) {
        
                history.push('/sanjay-sub-not-del');

                
                
            }
        
        
        }
        

        deletesubcatergory(config);


    },[id,history]);


    return (
        <>
        <Typography>
            welocme to delete sub cater 
        </Typography>
        </>
    )
}
