import React,{useEffect,useState} from 'react';

import {Paper,Box, Typography, Button, Table, TableHead, TableRow, TableCell, TableBody, Grid} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';

import {useHistory} from 'react-router-dom';

import axios from 'axios';

import ProductTable from '../Admin/comp/ProductTable';

export default function AdminProducts() {
    const history=useHistory();

    const [alldata,setAllData]=useState([]);


    useEffect(()=>{
        

        let config={
            method:"get",
            url:'/api/getproduct',

        }
        async function FetchProductData(input){

            try {
                
                const result=await axios(input);
    
                setAllData(result.data);
    
            } catch (error) {
    
              history.push('/page-not-found');

    
                
            }
        }

FetchProductData(config);

    },[history]);
    

   


    function bg(e){

        e.preventDefault();
        
        history.push('/dashboard');


    }


    function addproductform(){

        
        
        history.push('/productform');
        
        
        
            }

    return (
        <>
        <Paper  style={{padding:'24px 16px ',textTransform:'capitalize'}}>
        <Box style={{textAlign:'center',padding:'0px 0px',margin:'24px 0px'}}>
           <Typography variant="h6">
           welcome to admin products page he mange it
           </Typography>
       </Box>
      
      <Grid container style={{padding:'16px 0px'}} justifyContent="space-between">
          <Grid item>
          <Button onClick={bg} href="/dashboard">
    Go Back 
</Button>
          </Grid>

          <Grid item >
          <Button startIcon={<AddIcon/>} onClick={addproductform}>
    Add New Product
</Button>
          </Grid>

      </Grid>

<Table>
    <TableHead>
        <TableRow>
            <TableCell>
                Name 
            </TableCell>
            <TableCell>
                Parent
            </TableCell>
            <TableCell>
              Reviews
            </TableCell>
            <TableCell>
                image
            </TableCell>
            <TableCell>
                Edit
            </TableCell>
            <TableCell>
                Delete
            </TableCell>
        </TableRow>
    </TableHead>
    <TableBody>
        {
            alldata.map((item)=>{

                return (
                    <ProductTable key={item._id} _id={item._id} name={item.name} pcatergory={item.pcatergory}  image={item.image} reviews={item.reviews}>

                    </ProductTable>
                )
            })
        }
        
    </TableBody>
</Table>


        </Paper>
        
        </>
    )
}
