import {  Grid ,Box, Typography,Button,
     List} from '@material-ui/core';

import React,{useState} from 'react';


import { NavLink, useHistory } from 'react-router-dom';



import MyHomelink from './MyHomLink';


export default function Home() {


    const history=useHistory();

const[data]=useState([{
    id:"61b1dc484788a50f00d75fdc",
    name:"Appliances",
    image:"/images/c1.jpg",
    childs:[
        { id:"61b1e1274788a50f00d76051", name:"air conditioners" },
        { id:"61b1e1464788a50f00d76059", name:"air coolers" },
        { id:"61b1e1a34788a50f00d76061", name:"dishwashers" }
    ]

},{
    id:"61b1dc694788a50f00d75fe1",
    name:"Baby care",
    image:"/images/c2.jpg",
    childs:[
        { id:"61b1e1da4788a50f00d76069", name:"activity & play time" },
        { id:"61b1e21b4788a50f00d76071", name:"babay carriers" },
        { id:"61b1e2434788a50f00d76079", name:"baby clothing" }
    ]

},{
    id:"61b1dd2c4788a50f00d75ff5",
    name:"clothing & accessories",
    image:"/images/c3.jpg",
    childs:[
        { id:"61b1e2fe4788a50f00d76097", name:"babay" },
        { id:"61b1e2c44788a50f00d76089", name:"boys" },
        { id:"61b1e3234788a50f00d7609f", name:"girls" }
    ]

}]);

    return (
        <>


           <Grid container>

             
               <Grid item xs={12} sm={8} style={{display:'flex',alignItems:'center',flexDirection:'column',justifyContent:'center'}} >

                   <Typography variant="h5" style={{margin:'16px 8px',textAlign:'center'}}>
                   The Best Products in Every Catergory
                   </Typography>
<Typography style={{margin:'8px'}}>
We are Providing Best Products with Youtube Review Video
</Typography>
               </Grid>

<Grid item sm={4} >

    <Box>
        <img src="/images/v1.jpg" alt="ss" style={{width:'100%'}}>
        </img>
    </Box>

</Grid>
           </Grid>

           

<Grid container  spacing={3}  style={{margin:'16px auto'}}>

    <Grid item  xs={12} sm={4}>

<div>
<img src="/images/f1.jpg" alt="sd" style={{maxWidth:'100%',height:'300px',padding:'5% 25%'}}>
</img>

<Typography style={{textAlign:'center',padding:'16px'}} variant="h6" >
    Vivo V21 5G (Arctic White, 8GB RAM, 128GB Storage) 
    </Typography>

    </div>




    </Grid>
    <Grid item xs={12} sm={8}>
<iframe width="100%" height="100%" src="https://www.youtube-nocookie.com/embed/rhF75i0X3uQ" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
    </Grid>

</Grid>
   
   <Box style={{margin:'24px auto'}}>
       <Typography variant="h5">
           Popular Catergory
       </Typography>
   </Box>
   <Grid container spacing={3}  style={{padding:'8px 16px',textTransform:'capitalize'}}>
{
    data.map((item)=>{
        return(
            <Grid key={item.id} item xs={12} sm={4}>
                <NavLink to={`/catergories/${item.id}`} style={{textDecoration:'none',color:'inherit'}}>

                
                 <Typography variant="h6" style={{marginBottom:'8px',marginLeft:'16px'}}>
{
    item.name
}
           </Typography>
          
           <Box style={{height:'180px'}}>
    <img src={item.image} alt={item.name} style={{maxWidth:'100%',height:'100%'}}>
    </img>

</Box>
</NavLink>
<List>
    {
        item.childs.map((i)=>{

            return(
                
<MyHomelink key={i.id} id={i.id} name={i.name}></MyHomelink>
               
            )
        })
    }
</List>
                </Grid>
        )
    })
}
      
   </Grid>
    
  <Box textAlign="center" padding="16px 0px">
      <Button size="large" onClick={(e)=>{

          e.preventDefault();

          history.push('/catergories')

      }} href="/catergories">View All</Button>
  </Box>
          
        </>
    )
}
