import { Button, FormControl, Input, InputLabel, Paper, Typography } from '@material-ui/core'
import axios from 'axios';
import React,{useState,useEffect} from 'react';


import {useParams,useHistory} from 'react-router-dom';


export default function EditCatergory() {

    const {id}=useParams();

    const [status,setStatus]=useState(false);

 

 const[name,setName]=useState('');

const history=useHistory();


    useEffect(()=>{

        
let getconfig={

    method:'get',
    url:`/api/getcatergory/${id}`,

};
async function getcatergory(input){

    try {

        const result=await axios(input);

        

        setName(result.data.name);

        
        
        
    } catch (error) {
        
       history.push('/categor-get-edit-not');

    }
}
getcatergory(getconfig);


    },[id,history]);

    async function editcaterogry(input){

        try {
            
            await axios(input);


            setStatus(true);


        } catch (error) {
            
           
        }
    }

  

function handelName(e){

    setName(e.target.value);


}

function handelSubmit(e){

    e.preventDefault();

   
    let config={

        method:'put',
        url:`/api/updatecatergory/${id}`,
        data:{
            name:name
        },


    };

    editcaterogry(config);



}

function backmenu(){

history.goBack();

}


    return (
       <>
       {
           status?<div>
               <Typography>
                   item edit sucessfuly completed
               </Typography>
               <Button onClick={backmenu}>
                   Go back 
               </Button>
           </div>:<Paper>
       <Typography>
           welcome edit catergory js 
       </Typography>

       <form onSubmit={handelSubmit}>
           <FormControl>
               <InputLabel>
               name of catergory 
               </InputLabel>
               <Input type="text" value={name} onChange={handelName}>
               </Input>
           </FormControl>
<Button type="submit">
    Submit
</Button>
       </form>
       </Paper>

       }
       
       
       </>
    )
}
