
import axios from 'axios';
import React,{useEffect, useState} from 'react'

export default function GetVideo(props) {

    const [vidata,setVidata]=useState({videoId:''});

    useEffect(()=>{

        

        let str=props.data;

        let vidoe=str.concat(" review");


        async function FetchVideo(input){

            try {

                const result=await axios(input);

            

               setVidata(result.data[0].id);


             



                
            } catch (error) {
                
          

            }

        }


        let config={
            method:'get',
            url:'/api/public/getvideo',
            params:{
                search:vidoe,
            },

        };


        FetchVideo(config);

    },[props.data]);

    return (
        <>


{
    vidata.videoId&&<iframe width="100%" height="315" 
    src={`https://www.youtube-nocookie.com/embed/${vidata.videoId}`} 
    title="YouTube video player" 
    frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
    allowFullScreen></iframe>
    

}


        </>
  
  )



}
