import { Typography ,Paper} from '@material-ui/core';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Bar } from 'react-chartjs-2';
import { useHistory } from 'react-router';





export default function SubCaterChart() {

  const history=useHistory();




  const [data,setData]=useState({
    labels: [],
    datasets: [
      {
        label: 'Total Number Of Products',
        data: [],
      backgroundColor:['rgba(255, 99, 132, 0.2)',  'rgba(54, 162, 235, 0.2)',
        'rgba(255, 206, 86, 0.2)',
          'rgba(75, 192, 192, 0.2)',
          'rgba(153, 102, 255, 0.2)',
          'rgba(255, 159, 64, 0.2)',
          'rgba(155, 159, 64, 0.2)',
  ],
    
  
      },
    ],
  });


  useEffect(()=>{

   
    let config={

      method:'get',
      url:'/api/dashboard/catergory-data',

    };

    async function FecthCatergoryData(input){

      try {
  
        const result=await axios(input);
  
     

let labar=result.data.map((item)=>{

  return (
    item.name
  )
});


let dataar=result.data.map((item)=>{

  let fg=item.data;

 


  return (
    fg
  )
});

setData({
  labels: labar,
  datasets: [
    {
      label: 'Total Number Of Products',
      data: dataar,
    backgroundColor:['rgba(255, 99, 132, 0.2)',  'rgba(54, 162, 235, 0.2)',
      'rgba(255, 206, 86, 0.2)',
        'rgba(75, 192, 192, 0.2)',
        'rgba(153, 102, 255, 0.2)',
        'rgba(255, 159, 64, 0.2)',
        'rgba(155, 159, 64, 0.2)',
], },
  ],
});

        
  
  
        
      } catch (error) {
       
        history.push('/page-not-found');
        
        
      }
  
    }

    FecthCatergoryData(config);


  },[history]);


  
  return (
    <>
    <Paper style={{margin:'48px 10%',textAlign:'center'}}>
        <Typography>Table of Total number of Products In Each Catergory</Typography>
    <Bar data={data}/>
    </Paper>
    
  </>
  )
}
