import { Button, FormControl, FormLabel, Input, Paper, Typography } from '@material-ui/core';
import axios from 'axios';
import React,{useEffect,useState} from 'react';


import {useParams,useHistory} from 'react-router-dom';



export default function EditProduct() {

const {id}=useParams();

const history=useHistory();

const [name,setName]=useState('');

const [description,setDescription]=useState('');

const [reviews,setReview]=useState('');




const [buynow,setBuyNow]=useState('');

const [status,setStatus]=useState(false);


useEffect(()=>{

  

    let getconfig={

        method:'get',
        url:`/api/getproduct/${id}`,

    };
    async function getproduct(input){

        try {
            
            const result=await axios(input);
    
            
    
            setName(result.data.name);
    
            setDescription(result.data.description);
    
            setReview(result.data.reviews);
    
            setBuyNow(result.data.buynow);
    
        } catch (error) {
            
            history.push('/product-get-edit-not');
            
        }
    }

    getproduct(getconfig);

    

},[id,history]);

async function editproduct(input){

    try {
        
       await axios(input);


setStatus(true);



    } catch (error) {
        
        

    }
}




function handelSubmit(e){


    e.preventDefault();


    let config={
        method:'put',
        url:`/api/updateproduct/${id}`,
        data:{
            name:name,
            description:description,
            reviews:reviews,
           
            buynow:buynow
        },
    };

    editproduct(config);


}

function handelName(e){

    setName(e.target.value);

}

function handelDescription(e){

    setDescription(e.target.value);

}


function handelReview(e){

    setReview(e.target.value);

}



function handelBuynow(e){

    setBuyNow(e.target.value);

}

function backmenu(){

history.goBack();

}
    return (
        <>
        {
            status?<div>
                <Typography>
                    product edit sucessfully completed 
                </Typography>
                <Button onClick={backmenu}>
                    Go Back
                </Button>

            </div> :  <>
             <Paper>
 <div>
            welcome edit product page
        </div>

        <form onSubmit={handelSubmit}>
            <FormControl style={{width:'100%'}}>
                <FormLabel>
                    Name of product 
                </FormLabel>
                <Input type="text" value={name} onChange={handelName}>
                </Input>
            </FormControl>
          
            <FormControl style={{width:'100%'}}>
                <FormLabel>
                   Description of product 
                </FormLabel>
                <Input type="text" value={description} onChange={handelDescription}>
                </Input>
            </FormControl>
            <FormControl style={{width:'100%'}}>
                <FormLabel>
                  Review of product 
                </FormLabel>
                <Input type="number" value={reviews} onChange={handelReview}>
                </Input>
            </FormControl>
         
            <FormControl style={{width:'100%'}}>
                <FormLabel>
                Buy Now  of product 
                </FormLabel>
                <Input type="text" value={buynow} onChange={handelBuynow}>
                </Input>
            </FormControl>

            <Button type="submit">
                submit
            </Button>
        </form>
        </Paper>
            </>
           
        }
      
       </>
    )
}
