import { Button, ListItem } from '@material-ui/core'
import React from 'react'

import {useHistory} from 'react-router-dom';
export default function MyHomLink(props) {

    const history=useHistory();

    function handelClik(e){

e.preventDefault();

history.push(`/subcatergory/${props.id}`);


    }
    return (
       <>
       <ListItem>
           <Button href={`/subcatergory/${props.id}`} onClick={handelClik}>
{
    props.name
}
           </Button>
       </ListItem>
       </>
    )
}
