import { Box, Button, Paper, Typography } from '@material-ui/core'
import React,{} from 'react'

import { useHistory } from 'react-router-dom';

export default function PageNot() {

const history=useHistory();



function handelC(e){

    e.preventDefault();

    history.push('/');

}
    return (
     <>
     <Paper style={{padding:'100px 100px'}}>
         
         <Box style={{padding:'8px 8px'}} textAlign="center">
             <Typography variant="h6">

                 Page Not Found 

             </Typography>
         </Box>
         <Box style={{padding:'16px 16px'}} textAlign="center">
             <Typography variant="h6">

                 404

             </Typography>
         </Box>

         <Box style={{padding:'8px 8px'}} textAlign="center">
           
           <Button herf="/" onClick={handelC} >
Go To Home
           </Button>
         </Box>
     </Paper>
     </>
    )
}
