import React, { useEffect, useState } from 'react';
import { Line } from 'react-chartjs-2';
import {Paper} from '@material-ui/core';
import axios from 'axios';
import { useHistory } from 'react-router';


const options = {
  scales: {
  
  },
};



export default function VisitorInDay() {

  
const history=useHistory();

  const [data,setData]=useState({
    labels: [],
    datasets: [
      {
        label: 'new visitor in a year',
        data: [],
        fill: false,
        backgroundColor: 'rgb(205, 99, 132)',
        borderColor: 'rgba(135, 99, 132, 0.2)',
      },
    ],
  });

  

  useEffect(()=>{

  

    let config={
      method:'get',
      url:'/api/dashboard/user-data',

    };

    async function Fetchvisitorinyear(input){

      try {
  
        const result=await axios(input);
  
        

        let labelar=result.data.map((item)=>{

          return (
            item.name
          )
        });

        let dataar=result.data.map((item)=>{

          return (
            item.value
          )
        });

        setData({
          labels:labelar,
          datasets:[
            {
              label: 'new visitor in a year',
            data: dataar,
            fill: false,
            backgroundColor: 'rgb(205, 99, 132)',
            borderColor: 'rgba(135, 99, 132, 0.2)',
            }
          ]
        });
        

  
        
      } catch (error) {
        
        history.push('/page-not-found');
        
        
      }
  
    }

    Fetchvisitorinyear(config);


  },[history]);

  
  return (
    <>
    <Paper style={{margin:'48px 10%'}}>
    <Line data={data} options={options} />
    </Paper>
    </>
  )
}

