import React,{useEffect} from 'react';

import axios from 'axios';
import { Button, Typography,Paper ,Box} from '@material-ui/core';

import {useHistory} from 'react-router-dom';
import { useDispatch,useSelector } from 'react-redux';

import {login} from './AdminSlice';


export default function Admin() {

    const history=useHistory();

   const profile=useSelector(state=>state.loginData);

   const dispatch=useDispatch();

   

   

    useEffect(()=>{

       

        let config={
            method:'get',
            url:'/api/getadmin',
            
        };

        async function  FetchAdminProfile(input) {
        

            try {
                
                const result=await axios(input);
    
              
                
             dispatch(login(result.data));//{name:'',email:""}

    
    
            } catch (error) {
                
                history.push('/page-not-found');
                
    
            }
        }

        FetchAdminProfile(config);


    },[dispatch,history]);
    
   function backDash(e){

    e.preventDefault();

    history.push('/dashboard');

   }
  
    
    return (
        <>
        <Paper style={{padding:'24px 16px ',textTransform:'capitalize'}}>
            <Box textAlign="center">
            <Typography variant="h6">
            welcome to admin profile page
        </Typography>
            </Box>
       {
           profile.status&&<Box style={{padding:'24px 16px'}}>

           <Typography style={{margin:'8px 0px'}}>
               Name of admin  {
                   profile.data.name
               }
           </Typography>
           
           <Typography style={{margin:'8px 0px'}}>
               Email of admin {
                   profile.data.email
               }
           </Typography>
           </Box>
       }


<Box style={{textAlign:'center',padding:'24px 0px'}}>
    <Button href="/dashboard" onClick={backDash}>
        Go to dashboard
    </Button>
</Box>

        </Paper>
       
   

  
        </>
    )
}
