import React,{useState,useEffect} from 'react';

import { NavLink, useHistory, useParams} from 'react-router-dom';

import axios from 'axios';
import { Box, Breadcrumbs, Typography,Button, Paper, Grid } from '@material-ui/core';




export default function AllCatergoryId() {

    const[alldata,setAllData]=useState({
        name:'',
        childs:[],
    });
    

    const history=useHistory();

    const {id}=useParams();

    useEffect(()=>{

       

        let config={
            method:'get',
            url:`/api/public/getallcatergorybyid/${id}`,

            
        };


        async function FetchAllCatergoryId(input){

            try {
                
                const result=await axios(input);
    
                
    
                setAllData(result.data);
                
               
    
    
            } catch (error) {
                
                
                history.push('/page-not-found');
              



    
            }
    
        }

        FetchAllCatergoryId(config);



    },[id,history]);

   

    return (
    <>
   <Box style={{padding:'24px 8px'}}>
        <Breadcrumbs>
       <Button>
           Home
       </Button>
       <Button>
           All Catergories
       </Button>
       <Button>
           {
               alldata.name
           }
       </Button>
        </Breadcrumbs>
        </Box>
   
   <Paper  style={{textTransform:'capitalize',marginBottom:'48px'}}>
   <Box style={{padding:'16px 8px'}}>
            <Typography variant="h5" component="h5">
                {
                    alldata.name
                }
            </Typography>
            </Box>
           <Grid container spacing={3} style={{padding:'16px 16px'}}>
{
    alldata.childs.map((item)=>{

        return(
            <Grid key={item._id} item xs={12} sm={3} style={{margin:'16px 0px'}}>
<Box>


                <NavLink to={`/subcatergory/${item._id}`} style={{textDecoration:'none',color:'inherit'}}>

<Grid container spacing={1} alignItems="center">

<Grid item xs={6}>


<img src={item.image} alt={item.name} style={{maxWidth:'100%'}}>
                                        </img>

</Grid>
<Grid item xs={6}>
<Typography variant="h6">
    {
        item.name
    }
</Typography>
</Grid>
</Grid>
                </NavLink>
                </Box>
                </Grid>
        )
    })
}
   </Grid>
   </Paper>
   
   
    </>
    )
}
