import React,{useState,useEffect} from 'react';

import {Paper, Typography,Table,TableHead, TableRow, TableCell, TableBody,
    Box,
    Button} from '@material-ui/core';

import axios from 'axios';

import {useHistory} from 'react-router-dom';


import UserTabel from '../Admin/comp/UserTable';



export default function User() {

    const history=useHistory();

    const [alldata,setAllData]=useState([]);

    useEffect(()=>{

       

let config={
    method:'get',
    url:'/api/getuser',
};

async function FetchAdminUserData(input){
    try {
        
        const result=await axios(input);


        setAllData(result.data);

    } catch (error) {


        history.push('/page-not-found');


        
    }

}

FetchAdminUserData(config);

    },[history]);

   

    function bg(e){
        e.preventDefault();

history.push('/dashboard');


    }


    return (
       <>
       <Paper style={{padding:'24px 8px'}}>
       
<Box textAlign="center">
<Typography variant="h6">user table is mangane from it </Typography>


</Box>

<Box style={{padding:"16px 32px ",margin:'32px 0px'}}>
    <Button onClick={bg}>
        Go Back
    </Button>
</Box>

  <Table>
<TableHead>
    <TableRow>
        <TableCell>
            id 
        </TableCell>
        <TableCell>
            Email of user 
        </TableCell>
        <TableCell>
            Edit
        </TableCell>
        <TableCell>
            Delete
        </TableCell>

    </TableRow>
</TableHead>
<TableBody>
    {
        alldata.map((item)=>{
return(
    <UserTabel key={item._id} _id={item._id} email={item.email}>

    </UserTabel>
)
        })
    }
</TableBody>

  </Table>
  
       </Paper>
       
       </>
    )
}
