import {  TableCell,IconButton, TableRow, Typography } from '@material-ui/core';
import React,{useState} from 'react';

import EditIcon from '@material-ui/icons/Edit';

import DeleteIcon from '@material-ui/icons/Delete';

import {useHistory} from 'react-router-dom';


export default function CaterTable(props) {

    const [data]=useState(props);

    const history=useHistory();

    function EditCatergory(){

        history.push(`/dashboard/catergory/edit/${data._id}`);

    }
    

    function DeleteCatergory(){
        history.push(`/dashboard/catergory/delete/${data._id}`);

    }


    return (
        <>
      
            <TableRow>
<TableCell>
<Typography>
    {
        data.name
    }
</Typography>
</TableCell>
<TableCell>

    <img src={data.image} alt={data.name} style={{maxWidth:'100%',height:'200px'}}>
    </img>

</TableCell>
<TableCell>
<IconButton onClick={EditCatergory}>
<EditIcon></EditIcon>
                       </IconButton>
</TableCell>
<TableCell>
<IconButton onClick={DeleteCatergory}>
<DeleteIcon></DeleteIcon>
                       </IconButton>
</TableCell>
            </TableRow>
      
        </>
    )
}
