
import React, { useEffect, useState } from 'react';
import { Button, FormControl, Input, InputLabel, Paper, Typography } from '@material-ui/core';

import {useParams,useHistory} from 'react-router-dom';
import axios from 'axios';



export default function EditSubcater() {


    const{id}=useParams();

    const history=useHistory();


 const [status,setStatus]=useState(false);


   const [name,setName]=useState('');

  



      useEffect(()=>{

       
let getconfig={

    method:'get',
    url:`/api/getsubcatergory/${id}`,

};

async function getsubcatergory(input){

    try {
        
        const result=await axios(input);

        setName(result.data.name);
       

    } catch (error) {
        
        history.push('/subcat-get-edit-not');
        

    }
}

getsubcatergory(getconfig);




    },[id,history]);

async function editsubcatergory(input){

    try {

      await axios(input);

      

setStatus(true);


        
    } catch (error) {


        
    }


}



function handelSubmit(e){


    e.preventDefault();


    console.log("button edit submit  clicked");


    let config={
        method:'put',
        url:`/api/updatesubcatergory/${id}`,
        data:{
            name:name,
           
        },

    };

    editsubcatergory(config);



}

function handelName(e){

    setName(e.target.value);

}



function backmenu(){

    history.goBack();

}
    return (
        <>
        {
            status?<div>
                <Typography>
                    edit is sucessfully done 
                </Typography>

                <Button onClick={backmenu}>
                    Go Back
                </Button>
            </div>: <Paper>


            <Typography>
                welcome to subcater edit subacterjs
            </Typography>

            <Button onClick={backmenu}>
                Back to pervous satatus 
            </Button>
            <form onSubmit={handelSubmit}>
                <FormControl>
    
                    <InputLabel>
                    name of catergory
                    </InputLabel>
    
                    <Input type="text" value={name} onChange={handelName}>
                    </Input>
                    
                </FormControl>
    
    
                <Button type="submit">
                    Submit 
                </Button>
            </form>
            </Paper>
        }
       
       
        </>
    )
}
