import React,{useState,useEffect} from 'react';

import {Paper, Typography,Box,Button, Table, TableHead, TableRow, TableCell, TableBody, Grid} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';


import {useHistory} from 'react-router-dom';

import axios from 'axios';

import SanjaySubTable from '../Admin/comp/SanjaySubTable';





export default function AdminSanjaySubcatergory() {

    const history=useHistory();

    const [alldata,setAllData]=useState([]);


    useEffect(()=>{
       
        let config={
            method:"get",
            url:'/api/getsansuball',

        }

        async function FetchSubCaterData(input){

            try {
                
                const result=await axios(input);
    
                
                setAllData(result.data);
    
    
    
            } catch (error) {
    history.push('/page-not-found');
    
                
            }
        }

        FetchSubCaterData(config);

        

    },[history]);

  


    function addsubform(){

        history.push('/sanjaysubform');
        
        
        
            }
            function bg(e){

                e.preventDefault();
                
                history.push('/dashboard');
        
        
            }
        

    return (
       <>
       <Paper style={{padding:'24px 16px ',textTransform:'capitalize'}}>
     
       <Box style={{textAlign:'center',padding:'0px 0px',margin:'24px 0px'}}>
           <Typography variant="h6">
           welcome to admin sanjay  subcatergory page
           </Typography>
       </Box>

       <Grid container style={{padding:'16px 0px'}} justifyContent="space-between">

<Grid item >
<Button href="/dashboard" onClick={bg}>
    Go back 
</Button>
</Grid>
<Grid item >
<Button startIcon={<AddIcon/>} onClick={addsubform}>
Add subcatergory
          </Button>
</Grid>

       </Grid>
      
       
       <Table>
           <TableHead>
               <TableRow>
                   <TableCell>
Name

                   </TableCell>
                   <TableCell>
Parent

                   </TableCell>
                   <TableCell>
Image

                   </TableCell>
                   <TableCell>
                       Edit
                   </TableCell>
                   <TableCell>
                       Delete
                   </TableCell>

               </TableRow>
           </TableHead>
           <TableBody>
              {
                  alldata.map((item)=>{

                    return(
                  
                   
                     <SanjaySubTable _id={item._id} key={item._id} name={item.name} parent={item.parent} image={item.image} ></SanjaySubTable>
                        
                        
                      
                    )
                  })
              }
           </TableBody>
       </Table>
      
       </Paper>
     
       </>
    )
}
