
import {createSlice} from '@reduxjs/toolkit';


export const adminSlice=createSlice({
    name:'admin data',
    initialState:{

       status:false,
       data:{
           name:'',
           email:'',
       },
    },
    reducers:{
        login:(state,payload)=>{
// payload.payload

state.status=true;

         state.data=payload.payload;//{name:"",email:""}
           
        },
        logout:(state)=>{

            state.status=false;
            state.data={
                name:"",
                email:"",
            }


        }
    },
});

export const {login,logout} =adminSlice.actions;


export default adminSlice.reducer;


