import React,{useState} from 'react';


import {  TableCell,IconButton, TableRow, Typography } from '@material-ui/core';

import EditIcon from '@material-ui/icons/Edit';

import DeleteIcon from '@material-ui/icons/Delete';

import {useHistory} from 'react-router-dom';


export default function ProductTable(props) {

    const [data]=useState(props);

    const history=useHistory();

    function EditProduct(){

        history.push(`/dashboard/product/edit/${data._id}`);

    }

    function DeleteProduct(){
        history.push(`/dashboard/product/delete/${data._id}`);

    }


    return (
      <>
 <TableRow>
        <TableCell>
        <Typography>
            {
                data.name
            }
        </Typography>
        </TableCell>
        <TableCell>
        <Typography>
            {
                data.pcatergory
            }
        </Typography>
        </TableCell>
        
        <TableCell>
        <Typography>
            {
                data.reviews
            }
        </Typography>
        </TableCell>

<TableCell>
    <img src={data.image} alt={data.name} style={{maxWidth:'100%',height:'200px'}}>
    </img>
</TableCell>
        <TableCell>
        <IconButton onClick={EditProduct}>
        <EditIcon></EditIcon>
                               </IconButton>
        </TableCell>
        <TableCell>
        <IconButton onClick={DeleteProduct}>
        <DeleteIcon></DeleteIcon>
                               </IconButton>
        </TableCell>
                    </TableRow>
      </>
    )
}
