import React,{useState,useEffect} from 'react';


import {Paper, Typography,Box,Button, Table, TableHead, TableRow, TableCell, TableBody, Grid} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';


import {useHistory} from 'react-router-dom';

import axios from 'axios';

import CaterTable from '../Admin/comp/CaterTable';




export default function AdminCatergory() {


    const history=useHistory();

    const [alldata,setAllData]=useState([]);


    useEffect(()=>{
       
        let config={
            method:"get",
            url:'/api/getcatergory',

        }

        async function FetchCatergoryData(input){

            try {
                
                const result=await axios(input);
    
                setAllData(result.data);
    
            } catch (error) {
    
                history.push('/page-not-found');
    
            }
        }

        FetchCatergoryData(config);

    },[history]);

   


    function addcaterform(){


history.push('/catergoryform');

    }

    function bg(e){

        e.preventDefault();
        
        history.push('/dashboard');


    }

    return (
      <>
     <Paper style={{padding:'24px 16px ',textTransform:'capitalize'}}>
   
       <Box style={{textAlign:'center',padding:'0px 0px',margin:'24px 0px'}}>
           <Typography variant="h6">
           welcome to admin admincatergory page
           </Typography>
       </Box>

       <Grid container style={{padding:'16px 0px'}} justifyContent="space-between">
          <Grid item>
          <Button onClick={bg} href="/dashboard">
    Go Back 
</Button>
          </Grid>

          <Grid item >
          <Button startIcon={<AddIcon/>} onClick={addcaterform}  >
Add catergory
          </Button>
          </Grid>

      </Grid>
       
       
       <Table>
           <TableHead>
               <TableRow>
                   <TableCell>
Name

                   </TableCell>
                  
                   <TableCell>
Image

                   </TableCell>
                   <TableCell>
                       Edit
                   </TableCell>
                   <TableCell>
                       Delete
                   </TableCell>

               </TableRow>
           </TableHead>
           <TableBody>
               
              {
                  alldata.map((item)=>{

                    return(
<CaterTable key={item._id} _id={item._id} name={item.name} image={item.image}></CaterTable>
                    )
                  })
              }
           </TableBody>
       </Table>

       
        
       </Paper>


      </>
    )
}
