import React,{useState} from 'react';


import {  TableCell,IconButton, TableRow, Typography } from '@material-ui/core';

import EditIcon from '@material-ui/icons/Edit';

import DeleteIcon from '@material-ui/icons/Delete';

import {useHistory} from 'react-router-dom';


export default function SubcaterTable(props) {

    const [data]=useState(props);

    const history=useHistory();

    function EditSubCatergory(){

        history.push(`/dashboard/sanjaysub/edit/${data._id}`);

    }

    function DeleteSubCatergory(){
        history.push(`/dashboard/sanjaysub/delete/${data._id}`);

    }


    return (
      <>
        <TableRow>
        <TableCell>
        <Typography>
            {
                data.name
            }
        </Typography>
        </TableCell>
        
<TableCell>
<Typography>
    {data.parent}
</Typography>
</TableCell>


        <TableCell>
           <img src={data.image} alt={data.name} style={{maxWidth:'100%',height:'200px'}}>
           </img>
        </TableCell>
        <TableCell>
        <IconButton onClick={EditSubCatergory}>
        <EditIcon></EditIcon>
                               </IconButton>
        </TableCell>
        <TableCell>
        <IconButton onClick={DeleteSubCatergory}>
        <DeleteIcon></DeleteIcon>
                               </IconButton>
        </TableCell>
                    </TableRow>
                    </>
    )
}
