import { FormControl, FormLabel,Box,MenuItem ,Button,Grid, Input, Paper, TextField, Typography } from '@material-ui/core'
import axios from 'axios';
import React,{useState,useEffect} from 'react';

import {useHistory} from 'react-router-dom';



export default function SanjaySubCaterForm() {
    const [currency, setCurrency] = React.useState('');
    
const [currencies,SetC] =useState([]);

    useEffect(()=>{

      async function FectParent(){

        try {
let result=[];
           result=await axios({
            method:'get',
            url:'/api/public/data3',
          });

         

          let ar=[];
          ar=result.data.map((item)=>{

            return {
              value:item,
              label:item,
            }
          });

          SetC(ar);

          
        } catch (error) {
         
        }

      }

      FectParent();
      


    },[]);
    const handleChange = (event) => {
        setCurrency(event.target.value);
      };
    
      const [er,setER]=useState(false);

  
      const history=useHistory();

const[name,setName]=useState('');


const [image,setImage]=useState(null);


const [data,setData]=useState({
  status:false,
  message:'',
});
      
    
 async function handleSubmit(e){

    e.preventDefault();

    

    if(name&&image&&currency){

      

      let formdata=new FormData();

      formdata.append('image',image);
      formdata.append('name',name);

      formdata.append('parent',currency);


      let config={
        method:'post',
        url:'/api/addsansubcater',
        data:formdata
   };


   try {

    const result=await axios(config);

setData({
  status:true,
  message:result.data._id
})
     
   } catch (error) {
    
    setER(true);
    
  

   }


    }





  }

  function handelName(e){

    setName(e.target.value);

  }


  function handelImage(e){

  setImage(e.target.files[0]);

  }
    return (
        <>
    
        <Paper style={{paddingBottom:"48px",paddingTop:"48px"}}>

          {
            data.status ?
            <Box style={{padding:'0px 16px',textTransform:'capitalize'}}>
            
            <Box style={{padding:'8px 0px'}}>
            <Typography variant="h6">
                item add suceesfully

              </Typography>
            </Box>
             
             <Box style={{padding:'8px 0px'}}>
             <Typography>
               Id of Item is  {
                  data.message
                }
              </Typography>
             </Box>
             
              <Button onClick={()=>{
                history.goBack()
              }}>
                go back
              </Button>
            </Box>
             
            :<>
            <Box textAlign="center">
    <Typography variant="h6">
        Subcatergory Form
    </Typography>
    </Box>

    {
      er&&<div>
        <Typography>
          validation error fill all details
        </Typography>
      </div>
    }
            <Grid container justifyContent="center">
<Grid item >
<form  onSubmit={handleSubmit}>
    <FormControl style={{width:'100%',margin:'16px auto'}}>
        <FormLabel>
            Name of subcatergory 
        </FormLabel>
        <Input type="text" value={name} onChange={handelName} ></Input>
    </FormControl>
<FormControl  style={{width:'100%',margin:'16px auto'}}>
    <FormLabel>
        Select Parent Sub of  Catergory
    </FormLabel>
<TextField value={currency} select  onChange={handleChange}>
    {currencies.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label}
            </MenuItem>
          ))}

    </TextField>
</FormControl>
   
    <FormControl  style={{width:'100%',margin:'16px auto'}}>

        <FormLabel>
            Image of subcatergory
        </FormLabel>
<Input type="file" onChange={handelImage}>
</Input>
    </FormControl>
    <Button type="submit" >
        Submit
    </Button>

</form>
</Grid>
            </Grid>
            </>
          }

        </Paper>
        </>
    )
}
