import React,{useState,useEffect} from 'react';

import {Paper, Typography,Box,Button, Table, TableHead, TableRow, TableCell, TableBody, Grid} from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';


import {useHistory} from 'react-router-dom';

import axios from 'axios';

import SubcaterTable from '../Admin/comp/SubcaterTable';



export default function AdminSubcatergory() {

    const history=useHistory();

    const [alldata,setAllData]=useState([]);


    useEffect(()=>{
       
        let config={
            method:"get",
            url:'/api/getsubcatergory',

        }

        async function FetchSubCaterData(input){

            try {
                
                const result=await axios(input);
    
                
    
                setAllData(result.data);
    
    
    
            } catch (error) {
    
               history.push('/page-not-found');
               
    
                
            }
        }

        FetchSubCaterData(config);

        

    },[history]);

   


    function addsubform(){

        
        history.push('/subcatergoryform');
        
        
        
            }
            
    function bg(e){

        e.preventDefault();
        
        history.push('/dashboard');


    }


    return (
       <>
       <Paper style={{padding:'24px 16px ',textTransform:'capitalize'}}>
    
       <Box style={{textAlign:'center',padding:'0px 0px',margin:'24px 0px'}}>
           <Typography variant="h6">
           welcome to admin subcatergory page
           </Typography>
       </Box>
      
       <Grid container style={{padding:'16px 0px'}} justifyContent="space-between">
          <Grid item>
          <Button onClick={bg} href="/dashboard">
    Go Back 
</Button>
          </Grid>

          <Grid item >
          <Button startIcon={<AddIcon/>} onClick={addsubform}>
Add subcatergory
          </Button>
          </Grid>

      </Grid>
       <Table>
           <TableHead>
               <TableRow>
                   <TableCell>
Name

                   </TableCell>
                   <TableCell>
Parent

                   </TableCell>
                   <TableCell>
Image

                   </TableCell>
                   <TableCell>
                       Edit
                   </TableCell>
                   <TableCell>
                       Delete
                   </TableCell>

               </TableRow>
           </TableHead>
           <TableBody>
              {
                  alldata.map((item)=>{

                    return(
                  
                   
                        <SubcaterTable key={item._id} _id={item._id} name={item.name} parent={item.parent} image={item.image}>

</SubcaterTable>
                        
                        
                      
                    )
                  })
              }
           </TableBody>
       </Table>
      
       </Paper>
     
       </>
    )
}
