import { Typography } from '@material-ui/core';

import React,{useEffect} from 'react';

import {useParams,useHistory} from 'react-router-dom';

import axios from 'axios';


export default function DeleteCatergory() {

    const {id}=useParams();

    const history=useHistory();


  


    useEffect(()=>{

        let config={
            method:'delete',
            url:`/api/deletecatergory/${id}`
        };

        async function deletecatergory(input){


            try {
                
                await axios(input);
    
    // history .goback work 
    history.goBack();
    
            } catch (error) {
                
                history.push('/catergory-not-del');
                
            }
        }

        


        deletecatergory(config);

    },[id,history]);


 
    return (
        <>
        <Typography>
            welcome to delete catergory
        </Typography>
        </>
    )
}
