import React, { useEffect, useState } from 'react';

import {NavLink, useLocation} from 'react-router-dom';

import axios from 'axios';
import { Typography,Paper,Box, Grid } from '@material-ui/core';




export default function SubcaterQuery() {

    const l=useLocation();

const[r,setResult]=useState(0);


  const [data,setData]=useState([]);


useEffect(()=>{
   
     const s=l.search.slice(1);

     let config={
          method:'get',
        url:'/api/public/getsubbytext',
        params:{
            search:s
        },
     };
     async function FetchDataSubcater(input) {

        try {
            
            const result=await axios(input);
            
             setData(result.data);
setResult(result.data.length);


        } catch (error) {

           

        }
        
    }
    FetchDataSubcater(config);
    
},[l]);


  

    return (
      <>
      <Paper style={{textTransform:'capitalize',padding:'24px 16px'}}>
      <Typography variant="h6" style={{margin:'8px auto'}}>
   Result found {r} 

</Typography>

<Grid container spacing={3} style={{padding:'24px 0px'}} justifyContent="space-around">
{
    data.map((item)=>{

        return(
            <Grid item key={item._id} xs={12} sm={3} style={{margin:'12px 0px'}}>

<NavLink to={`/products/${item._id}`} style={{color:'inherit',textDecoration:'none'}}>

<Box>
    <img src={item.image} alt={item.name} style={{maxWidth:'100%'}}>
    </img>
</Box>
<Typography variant="h6">
    {
        item.name
    }

</Typography>
</NavLink>
                </Grid>
        )
    })
}
</Grid>

      </Paper>
      
      </>
    )
}
