import React,{useState} from 'react';

import { Button, Grid, Paper, TextField,Typography,Box} from '@material-ui/core';
import axios from 'axios';


export default function Suscribe() {

const [email,setEmail]=useState('');

const [status,setStatus]=useState(false);

const[er,setEr]=useState(false);


function handelEmail(e){

    setEmail(e.target.value);

}

async function handelSubmit(e){

e.preventDefault();

console.log(email);

if(email){

    let config={
        method:'post',
        url:'/api/adduser',
        data:{
            email:email
        }
    };

    try {
        
        const result=await axios(config);

        console.log("user added sucessfuly");
        console.log(result.data);
        setStatus(true);
    
    } catch (error) {
        
        console.log("error in adding user");

        console.log(error);

        setEr(true);


    }


}else{
    console.log("form is empty");
    
}

}
    return (
      <>
      
      
        <Paper style={{padding:'24px 8px',margin:'12px 0px'}}>

        {
            status?<Box textAlign="center" style={{padding:"24px 0px",textTransform:'capitalize'}}>

<Typography variant="h6">
Thanks for Subscribe
</Typography>
            </Box>:<>
            <Box textAlign="center" style={{padding:'8px 0px'}}>
          <Typography variant="h5" style={{margin:'8px 0px '}}>
             Suscribe to our weekly Newsletters
         </Typography>
         <Typography  style={{margin:'8px 0px '}}>
         Get exclusive articles, recommendations, shopping tips, and sales alerts

         </Typography>
          </Box>
        
          {
    er&&<Box textAlign="center" style={{padding:'16px 0px'}}>
 <Typography color="secondary">
        Email is Already Exist..
        </Typography>
    </Box>
   
}
<Grid container style={{padding:'12px 0px'}} onSubmit={handelSubmit} component="form" spacing={2} justifyContent="center" >

<Grid item  >
<TextField value={email} onChange={handelEmail} required type="email" variant="outlined" placeholder="Enter your email " size="small"></TextField>
</Grid>

<Grid item >
<Button type="submit">
    Sumbit
</Button>
</Grid>
</Grid>
            </>
        }
          
     
</Paper>
    
      </>
    )
}
