import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';



import IconButton from '@material-ui/core/IconButton';

import SearchIcon from '@material-ui/icons/Search';

import {Box, FormControl, Input } from '@material-ui/core';

import {useHistory} from 'react-router-dom';



const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    alignItems: 'center',
justifyContent:'center',


    
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 10,
  
  },
  divider: {
    height: 28,
    margin: 4,
  },
}));

export default function CustomizedInputBase() {
  const classes = useStyles();

  const [data,setData]=useState('');

 

  const history=useHistory();

  function handelSubmit(e){

e.preventDefault();




if(data){

  history.push(`/search?${data}`);
 
  


}
else{
  

}



  }

  function handelChange(e){

    setData(e.target.value);


  }


  return (
    <Box component="form" className={classes.root} onSubmit={handelSubmit}>
      
   <FormControl>
     <Input type="search" placeholder="Search Catergory " value={data} required onChange={handelChange}>
     </Input>
   </FormControl>
      <IconButton type="submit" className={classes.iconButton} aria-label="search">
        <SearchIcon />
      </IconButton>
      
    </Box>
  );
}
