import React,{useState} from 'react';

import axios from 'axios';
import { Box, Button, FormControl, Grid, Input, InputLabel, Typography } from '@material-ui/core';

import {useHistory} from 'react-router-dom';
export default function AdminLogForm() {


const [email,setEmail]=useState('');

const [password,setPassword]=useState('');

const[er,setEr]=useState(false);

const history=useHistory();

function handelEmail(e){

    setEmail(e.target.value);

}

function handelPassword(e){

    setPassword(e.target.value);

}


function handelSubmit(e){

    e.preventDefault();

 

    if(email&&password){

        let config={
            method:"post",
            url:'/api/loginadmin',
            data:{
    email:email,
    password:password,
            },
    
        };


FetchRefreshToken(config);


    }else{

       
    }
 



}


   
   


    async function FetchRefreshToken(input) {

        try {
            
            const result=await axios(input);

          

            localStorage.setItem('token',result.data.token);


            axios.defaults.headers.common['Authorization'] = result.data.token;

          

            history.push('/admin');
            
        } catch (error) {
            
           setEr(true);


        }
        
    }

    return (
       <>
       
       <Grid container justifyContent="center" >
<Grid item >
    <Typography>
        Login here
        </Typography>
        {
            er&&<Box textAlign="center">
            <Typography color="secondary">
                Invalid Creditentals
            </Typography>
        </Box>
        }
        
<form onSubmit={handelSubmit}>

<FormControl style={{width:'100%'}}>
    <InputLabel>
    Email:
    </InputLabel>
    <Input type="email" value={email} onChange={handelEmail}>
    </Input>

</FormControl>

<FormControl style={{width:'100%'}}>
    <InputLabel >
    Password
    </InputLabel>
    <Input type="password" value={password} onChange={handelPassword}>
    </Input>

</FormControl>

<Button type="submit">

Login 
</Button>
         
       </form>
</Grid>
       </Grid>
     

       </>
    )
}
