import React, { useState ,useEffect} from 'react';
import {  makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';

import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';

import AccountCircle from '@material-ui/icons/AccountCircle';

import MoreIcon from '@material-ui/icons/MoreVert';

import Serachbar from '../compontes/Searchbar';
import { Button,Box,  } from '@material-ui/core';
import { useHistory } from 'react-router';

import List from '@material-ui/core/List';



import Drawer from '@material-ui/core/Drawer';
import clsx from 'clsx';
import CloseIcon from '@material-ui/icons/Close';


import HomeList from './HomeList';
import axios from 'axios';

import{useSelector} from 'react-redux';





const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
  
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto',
    },
    [theme.breakpoints.down('xs')]: {
      display:'none',

    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '20ch',
    },
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
  first:{
    display:'none',
    [theme.breakpoints.down('xs')]: {
      display: 'block',
    },
  },
  header:{
    backgroundColor:theme.palette.background.paper,
    color:theme.palette.text.primary,


  },
  second:{
    display:'flex',
    alignItems:'center',

  },
  third:{
    cursor:'pointer',

},
list: {
  width: 280,
},
fullList: {
  width: 'auto',
},


}));

export default function PrimarySearchAppBar() {
  
  const admin_status=useSelector(state=>state.loginData);


  const[cat,setCat]=useState([]);

  useEffect(()=>{
    async function ft(){

      try {
        const result=await axios({
          method:'get',
          url:'/api/public/getallcatergory'
        });

       

        setCat(result.data);

      } catch (error) {

        // no error find when all catergory found 

        
      }
    }

    ft();

  },[])

  const history=useHistory();

  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleProfileMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };





  function handelAll(e){
    e.preventDefault();

    setAnchorEl(null);
    handleMobileMenuClose();
history.push('/catergories');


  }

  function handelAbout(e){
    
    e.preventDefault();

    setAnchorEl(null);
    handleMobileMenuClose();
    history.push('/about');

  }

  function handelHome(e){

    e.preventDefault();


    setAnchorEl(null);
    handleMobileMenuClose();
    history.push('/');

  }

  function handelProfile(){

    setAnchorEl(null);
    handleMobileMenuClose();
    history.push('/admin');
  }

  function handelLogout(){
    setAnchorEl(null);
    handleMobileMenuClose();
    history.push('/logout');

  }

  const menuId = 'primary-search-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handelProfile} >Profile</MenuItem>
      <MenuItem onClick={handelLogout} >Logout</MenuItem>
    </Menu>
  );

  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
  <Menu
     anchorEl={mobileMoreAnchorEl}
     anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
     id={mobileMenuId}
     keepMounted
     transformOrigin={{ vertical: 'top', horizontal: 'right' }}
     open={isMobileMenuOpen}
     onClose={handleMobileMenuClose}
   >
   
      
          {
            (admin_status.status)&&<div>
            
     
     <MenuItem onClick={handleProfileMenuOpen}>
       <IconButton
         aria-label="account of current user"
         aria-controls="primary-search-account-menu"
         aria-haspopup="true"
         color="inherit"
       >
         <AccountCircle />
       </IconButton>
       <p>Profile</p>
     </MenuItem>
     
     </div>
          } 

  {
    !(admin_status.status)&&<div>
 <MenuItem>
   
   <Button  onClick={handelAll}>
     All Catergories
   </Button>
   </MenuItem>
   <MenuItem>
   <Button onClick={handelAbout}>
     About Us
   </Button>
   </MenuItem>
   </div>
  }
  
   

 </Menu>
       


  );

  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >

<Box style={{display:'flex',justifyContent:'space-around',alignItems:'center',padding:'12px 0px'}}>
  <Typography variant="h6">
    Welcome User
  </Typography>

  <IconButton >
<CloseIcon fontSize="medium"></CloseIcon>
  </IconButton>
</Box>

<Box textAlign="center">
  <Typography variant="h6" >

    Search By Department

  </Typography>
  <List style={{paddingLeft:'32px'}}>

    {
      cat.map((item)=>{

        return(
          <HomeList key={item._id} name={item.name} id={item._id}>

          </HomeList>
        )
      })
    }

  </List>
  

</Box>

      <List style={{paddingLeft:"32px"}}>
        
      </List>
     
     
    </div>
  );


  
  return (
    <div className={classes.grow}>
      <AppBar position="static" className={classes.header}>
        <Toolbar>
          
        {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          
          <IconButton onClick={toggleDrawer(anchor, true)}>
            <MenuIcon>

            </MenuIcon>
          </IconButton>
          <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
       
      
            <Typography variant="h6"  onClick={handelHome} className={classes.third}>
            Best Review Guide
            </Typography>
         
       
          <div className={classes.search}>
            <Serachbar></Serachbar>
            </div>
          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            {
              (admin_status.status)?<>
               
              <Box style={{textTransform:'capitalize',padding:"4px 8px"}}>
              <Button>
               {
  admin_status.data.name
}
               </Button>
              </Box>
            
            <IconButton
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
              </>: <Box className={classes.second}>

       <Button href="/" onClick={(e)=>{
         e.preventDefault();
         history.push('/');
       }}>
         Home
       </Button>

  
            <Button href="/catergories" onClick={handelAll}>
              All Catergories
            </Button>
            
            <Button href="/about" onClick={handelAbout}>
              About Us 
            </Button>

            </Box>
            }
           

           

          
          </div>
          <div className={classes.sectionMobile}>
            <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MoreIcon />
            </IconButton>
          </div>
        </Toolbar>
        <Toolbar className={classes.first}>
<Serachbar></Serachbar>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
      {renderMenu}
    </div>
  );
}
