import React,{useState} from 'react';

import {Typography, Grid, Box, InputLabel, Paper, FormControl, Input, Button} from '@material-ui/core';
import axios from 'axios';

import {useHistory} from 'react-router-dom';


export default function CatergoryForm() {

 

const history=useHistory();

    const [name,setName]=useState('');

    const [image,SetImage]=useState(null);

const [status,setStatus]=useState(false);

  const[id,setId]=useState('');


  async function addcatergory(input){

    try {
        
        const result=await axios(input);

        
        setId(result.data._id);

        setStatus(true);
        


    } catch (error) {
        

    }
  }

  function handelSubmit(e){
    e.preventDefault();

    
    if(name&&image){

        // form is completed

        let formdata=new FormData();

        formdata.append('image',image);

        formdata.append('name',name);


        let config={

            method:'post',
            url:'/api/addcatergory',
            data:formdata
        
        };

        addcatergory(config);


    }
  }

  function handelName(e){

    setName(e.target.value);



  }

  function handelImage(e){
SetImage(e.target.files[0]);

  }

  function backmenu(){
history.goBack();

  }

    return (
      <>
      {
          status?
          <Paper style={{padding:'24px 16px',textTransform:'capitalize'}}>
            <Box style={{padding:'24px 0px'}}>
            <Typography variant="h6">
                  item is  added sucessfully
      
              </Typography>
            </Box>
            
            <Box style={{padding:'24px 0px'}}>
              <Typography>
              item is   {
                  id
                }
              </Typography>
            </Box>
              <Button onClick={backmenu}>
                  Go Back
              </Button>
          </Paper>
             
          : <Paper style={{paddingTop:"48px",paddingBottom:"48px"}}>
<Box textAlign="center">
<Typography>
    Catergory Form
</Typography>


</Box>
     

    <Grid container justifyContent="center">

        <Grid item>
            <form onSubmit={handelSubmit}>

<FormControl style={{width:"100%",margin:"16px auto"}}>
    <InputLabel>
    Name of Catergory
    </InputLabel>
    <Input type="text" value={name} onChange={handelName}>
    </Input>
</FormControl>
           <FormControl  style={{width:"100%",margin:"16px auto"}}>
               <InputLabel>
               Image
               </InputLabel>
               <Input type="file" onChange={handelImage}>
               </Input>
           </FormControl>
          <Button style={{margin:'16px'}} type="submit">
              Submit
          </Button>
            </form>

        </Grid>

    </Grid>
      </Paper>
    
      }
     
      
      </>
    )
}
