import { Breadcrumbs,Box, Button, List, ListItem,  Paper, Typography, Grid } from '@material-ui/core'
import React,{useEffect,useState} from 'react';

import { NavLink } from 'react-router-dom';

import axios from 'axios';

import MYPush from './MYPush';


export default function AllCatergory() {

    const [alldata,setAllData]=useState([]);

    useEffect(()=>{
       

        let config={
            method:"get",
            url:"/api/public/getallcatergory",


        };

        FetchAllCatergoryData(config);


    },[]);

    async function FetchAllCatergoryData(input) {

try {
    
    const result=await axios(input);


   

    setAllData(result.data);
    



} catch (error) {
    
    // no error find when all catergory is here 

}
        
    } 

    

    return (
        <>
        <Box style={{padding:'24px 8px'}}>
        <Breadcrumbs>
       <Button>
           Home
       </Button>
       <Button>
           All Catergories
       </Button>
        </Breadcrumbs>
        </Box>
       
        <Paper style={{textTransform:'capitalize',marginBottom:'48px'}}>
            <Box style={{padding:'16px 8px'}}>
            <Typography variant="h5" component="h5">
                All Catergories 
            </Typography>
            </Box>
           
<Grid container spacing={3} style={{padding:'16px 16px'}}>

{
                alldata.map((item)=>{

                    return(
                        <Grid key={item._id}  item xs={12} sm={4} style={{margin:'16px 0px'}} >

<Box>
    <NavLink to={`/catergories/${item._id}`} style={{textDecoration:'none',color:'inherit'}}>
    <Grid container spacing={1} >
                                <Grid item xs={6} >
                                    <Box >

                                        <img src={item.image} alt={item.name} style={{maxWidth:'100%'}}>
                                        </img>
                                    </Box>
                                    </Grid>
                                    <Grid item xs={6} >
                                        
                                        <Typography variant="h6">
                                            {
                                                item.name
                                            }
                                        </Typography>

                                        </Grid>
                                </Grid>
    </NavLink>
</Box>

<List>
    {
        item.childs.map((i)=>{

            return(
                <ListItem key={i._id}>
<MYPush name={i.name} id={`/subcatergory/${i._id}`} ></MYPush>
                </ListItem>
            )

        })
    }
</List>
                          
                       
 </Grid>        )
                })
            }
</Grid>

         
        </Paper>
        </>
    )
}
