import { Typography,Box, Grid } from '@material-ui/core';
import axios from 'axios';
import React,{useState,useEffect} from 'react';

import DatabaseCount from './DatabseCount';

import {useHistory} from 'react-router-dom';

export default function AllCount() {

    const [data,setData]=useState([]);// [{name:"total number of prduct ",data:4}]

const history=useHistory();

useEffect(()=>{

   

    let config={
        method:'get',
        url:'/api/dashboard/count-data'
    };

    async function FetchAllData (input){

        try {
    
            const result=await axios(input);
    
    
            setData(result.data);
    
            
        } catch (error) {
            
            history.push('/page-not-found');


    
        }
    
        
    }

    FetchAllData(config);
    


},[history]);



    return (
      <>
      <Box style={{padding:'16px 0px',margin:'16px 0px'}}>
      <Typography variant="h6">
        Databse  data in the form of  table
      </Typography>
      </Box>
     
     <Grid container spacing={2}>

         {
             data.map((item)=>{

                return (
                    <Grid item xs={12} sm={3} key={`1-2${item.name}`}>
 <DatabaseCount data={item.data} name={item.name} ></DatabaseCount>
                        </Grid>
                )
             })
         }

     </Grid>
      
      </>
    )
}
