import {  Paper, Typography,Box, List, ListItem, Button} from '@material-ui/core';
import React from 'react';

import {useHistory} from 'react-router-dom';
import VisitorInDayChart from './Chart/VisitorInDay';

import SubCaterChart from './Chart/SubCaterChart';

import ReadUserAdmin from './others/ReadUserAdmin';

import AllCount from './AdminReuse/AllCount';// it is completed

export default function Dashboard() {

const history=useHistory();

    function f1(e){
        e.preventDefault();
history.push('/dashboard');
    }
    function f2(e){
        e.preventDefault();
        history.push('/dashboard/user');
    }
    function f3(e){
        e.preventDefault();
        history.push('/dashboard/product');
    }
    function f4(e){
        e.preventDefault();
        history.push('/dashboard/sanjaysub');
    }
    function f5(e){
        e.preventDefault();
        history.push('/dashboard/subcatergory');

    }
    function f6(e){
        e.preventDefault();
        history.push('/dashboard/catergory');
    }
       
    return (
       <>
       <Paper style={{padding:'24px 16px ',textTransform:'capitalize'}}>

       <Box style={{textAlign:'center',padding:'8px 0px',margin:'8px 0px'}}>
           <Typography variant="h6">
           welcome to admin dashboard page
           </Typography>
       </Box>

       <Box style={{border:'1px solid',padding:'16px 0px'}}>
           <List style={{display:'flex'}}>
               <ListItem style={{padding:'0px'}}>
                 <Button href="/dashboard" onClick={f1}>
                     Dashboard
                 </Button>
               </ListItem>
               <ListItem style={{padding:'0px'}} >
                   <Button href="/dashboard/user" onClick={f2}>
                       User
                   </Button>
               </ListItem>
               <ListItem style={{padding:'0px'}} >
                   <Button href="/dashboard/product" onClick={f3}>
                       Products 
                   </Button>
               </ListItem>
               <ListItem style={{padding:'0px'}}>
                  <Button href="/dashboard/sanjaysub" onClick={f4}>
SanSubcater
                  </Button>
               </ListItem>
               <ListItem style={{padding:'0px'}} >
                  <Button href="/dashboard/subcatergory" onClick={f5}>
Sub Catergory
                  </Button>
               </ListItem>
               <ListItem style={{padding:'0px'}} >
                  <Button href="/dashboard/catergory" onClick={f6}>
 Catergory
                  </Button>
               </ListItem>
           </List>
       </Box>
  
  <AllCount></AllCount>
 <SubCaterChart></SubCaterChart>
    
    <VisitorInDayChart></VisitorInDayChart>

<ReadUserAdmin></ReadUserAdmin>

       </Paper>
       

       </>
    )
}
