import React,{useState,useEffect} from 'react'
import{useParams,NavLink,useHistory} from 'react-router-dom';
import axios from 'axios';
import { Button, Typography ,Breadcrumbs,Box, Paper, Grid,} from '@material-ui/core';

export default function AllSubCatergory() {

    const history=useHistory();

    const{id}=useParams();
    const[data,setData]=useState({
        name:'',
        childs:[],
        parent:{},
    });
    useEffect(()=>{
async function FectSub(){

    try {
        
        const result=await axios({
            method:'get',
            url:`/api/public/getsubcatergorybyid/${id}`
        });

    

        setData(result.data);


    } catch (error) {
       


        history.push('/page-not-found');
        
    }

}

FectSub();



    },[id,history]);
    return (
        <>

<Box style={{padding:'24px 8px'}}>
        <Breadcrumbs>
       <Button>
           Home
       </Button>
       <Button>
           All Catergories
       </Button>
       <Button>
           {
               data.parent.name
           }
       </Button>
       <Button>
           {
               data.name
           }
       </Button>
        </Breadcrumbs>
        </Box>
        
        <Paper  style={{textTransform:'capitalize',marginBottom:'48px'}}>
<Box style={{padding:'16px 8px'}}>
    <Typography variant="h5">
        {
            data.name
        }
    </Typography>
</Box>


<Grid container spacing={3}  style={{padding:'16px 16px'}}>
    {
        data.childs.map((item)=>{

            return(

                <Grid key={item._id} item xs={12} sm={3} style={{margin:'16px 0px'}}>

<NavLink to={`/products/${item._id}`} style={{textDecoration:'none',color:'inherit'}}>

<Grid container spacing={1} justifyContent="center" alignItems="center" >

    <Grid item xs={6}>
    <img src={item.image} alt={item.name} style={{maxWidth:'100%'}}>
                                        </img>
    </Grid>
    <Grid item xs={6}>
<Typography variant="h6">
    {
        item.name
    }
</Typography>
    </Grid>


</Grid>

</NavLink>

                    </Grid>
            )
        })
    }

</Grid>



        </Paper>

        </>
    )
}
