import React,{useState,useEffect} from 'react';

import axios from 'axios';

import {useParams,useHistory} from 'react-router-dom';
import { Grid,Box , Typography, Paper, Button } from '@material-ui/core';



import YouTubeButton from './YouTubeButton';


export default function SubCaterProduct() {
    const {id}=useParams();
const history=useHistory();

const [alldata,SetAllData]=useState([]);

const[name,setName]=useState({
    data:"",
    value:0
});

const[dt]=useState(FindDate());


function FindDate(){

    let d=new Date();

    return d.getFullYear();

}

useEffect(()=>{

   
    let config={
        method:'get',
        url:`/api/public/getproduct/${id}`,
        
    };

    async function FetchProductSubcater(input) {

        try {
            
            const result= await axios(input);
    
           

            let l=result.data.length;

            let v=result.data[0].pcatergory.name;

            setName({
                data:v,
                value:l
            });
         

            let ar=[];

let rf={};

            

             let i;

             for(i=0;i<result.data.length;i++){

                rf=result.data[i];

                rf.r=i+1;
                

                ar.push(rf);


             }



    SetAllData(ar);

    
    
        } catch (error) {
            
           


            history.push('/page-not-found');
            
    
        }
        
    }
    
     

    FetchProductSubcater(config);

},[id,history]);



    return (
        <>
        <Box style={{textTransform:'capitalize',padding:'12px 8px'}}>

<Typography variant="h5" style={{margin:'8px auto'}}>
    {
        name.value
    } Best 
</Typography>

<Typography variant="h5" style={{margin:'8px auto'}}>
    {
        name.data
    }  {" "}
 {
       dt
   }
</Typography>
        </Box>
      
       {
           alldata.map((item)=>{

            return(
                <Paper key={item._id} style={{textTransform:'capitalize',margin:'32px auto',padding:'16px 16px'}} >

<Grid container spacing={4}  alignItems="center">

    <Grid item xs={12} sm={3}>

<Typography component="span" variant="h5" style={{margin:'8px 8px'}}>
{
        item.r
    }
</Typography>
   
   

        <Box style={{height:'250px'}}>
          <img src={item.image} alt={item.name} style={{maxWidth:'100%',height:'100%'}}>
          </img>
        </Box>

    </Grid>
    <Grid item xs={12} sm={9}>
<Box style={{padding:'12px 4px'}} >

    <Typography variant="h5">
        {
            item.name
        }
    </Typography>
</Box>

<Box style={{padding:'12px 4px'}}>
    <Typography>
    {
        item.description
    }
    </Typography>
   
</Box>


<Grid container spacing={2} style={{padding:'16px 0px'}}>

<Grid item  >
<Button target="_blank" color="primary" variant="outlined" href={`${item.buynow}`}>
        Buy now
    </Button>
</Grid>
<Grid item xs>
<YouTubeButton name={item.name}></YouTubeButton>

</Grid>
</Grid>


   

    </Grid>

</Grid>


                </Paper>
            )
           })
       }

        </>
    )
}
