import { Button, FormLabel, Input, Paper,FormControl, Typography } from '@material-ui/core';
import axios from 'axios';
import React,{useEffect,useState} from 'react';

import {useParams,useHistory} from 'react-router-dom';



export default function EditUser() {

    const {id}=useParams();

    const history=useHistory();

    const [email,setemail]=useState('');

    const [status,setStatus]=useState(false);

const[myid,setId]=useState('');

    useEffect(()=>{

        let getconfig={
            method:'get',
            url:`/api/getuser/${id}`
        };

        async function getuser(input){

            try {
                
                const result=await axios(input);

    setemail(result.data.email);
    
            } catch (error) {
                
                history.push('/user-get-edit-not');
              
    
            }
        }
    
       getuser(getconfig);


    },[id,history]);

    async function edituser(input){

        try {

            const result=await axios(input);

            setId(result.data._id);


setStatus(true);


            
        } catch (error) {
            
           
        }


    }

   
function handelEmail(e){

    setemail(e.target.value);

}

function handelSubmit(e){

    e.preventDefault();


    let config={
        method:'put',
        url:`/api/updateuser/${id}`,
        data:{
            email:email
        },

    };

    edituser(config);



}

function backmenu(){

    history.goBack();

}

    return (
        <>
        {
            status?<div>
                <Typography>
                    Edit is sucessfulyy completed
                </Typography>

                <Typography>
                    {myid}
                </Typography>
                <Button onClick={backmenu}>
                    Go Back
                </Button>
            </div>:<>
            <Paper>
        <div>
            welcome to edit user js
        </div>

        <form onSubmit={handelSubmit}>
            <FormControl>
                <FormLabel>
                    email of user 
                </FormLabel>
                <Input type="email" value={email} onChange={handelEmail}>

                </Input>
            </FormControl>
            <Button type="submit">
                Submit
            </Button>
        </form>
        </Paper>
       
            </>
        }
       
        </>
    )
}
